div {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.recipes {
    padding-top: 15px;
    padding-right: 30px;
    padding-left: 30px;
    font-family: Verdana, Geneva, sans-serif;
}

.bowl-title {
    text-align: center;
    padding: 10px 10px;
}

.sub-title {
    color: tomato;
    padding-top: 15px;
}

.video-container {
	position:relative;
	padding-bottom:56.25%;
	padding-top:30px;
	height:0;
	overflow:hidden;
}

.video-container iframe, .video-container object, .video-container embed {
	position:absolute;
	top:0;
	left:0;
	width:100%;
	height:100%;
}
    
#map {
  height: 91vh;
  width: 100vw;
  position: fixed;
}
.leaflet-routing-container-hide .leaflet-routing-collapse-btn {
  position: absolute;
  left: 5.5px;
  top: 0.7vh;
  width: 18px;
  height: 18px;
  background-image: url(data:image/png;base64,);
}
h2 {
    text-align: center;
    padding: 10px 10px;
}

p {
    padding-top: 15px;
    padding-right: 30px;
    padding-left: 30px;
    font-family: Verdana, Geneva, sans-serif;
}

.container {
    padding-top: 15px;
	position: relative;
	max-width: 100%;
	height: auto;
    text-align: center;
    color: black;
	font-family: Verdana, Geneva, sans-serif;
	font-size: 14px;
}

.centered {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.container .btn {
	position: absolute;
    top: 100%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    background-color: #555;
    color: white;
    font-size: 16px;
    padding: 12px 24px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    text-align: center;
	font-family: Verdana, Geneva, sans-serif;
}

.container .btn:hover {
    background-color: tomato;
}
